import React from 'react';
import './index.css';
import Header from '../Header';

const Projects = () => {
    return(
        <div className="content">
            <Header/>
            <div className="info">
                <div className="title">
                    <h2>My CV</h2>
                </div>
                <iframe title='cv' src="/my-app/Vasil-Ivanov-CV.pdf" width="100%" height="1190px" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe> 
            </div>
        </div>
    )
}

export default Projects;